<template>
  <div class="charges">
    <section>
      <request
        v-for="request in requests"
        :key="request"
        :requestId="request._id"
        requestType="charges"
      >
        {{
          `username: ${request.username}
status: ${request.status}
type: ${request.type}
wallet: ${request.wallet}
charged amount: ${request.amountCharged}
requested in: ${request.createdAt}`
        }}
      </request>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import request from "../components/request.vue";
export default {
  name: "Charges",
  data() {
    return {
      requests: [],
    };
  },
  components: { request },
  async mounted() {
    let rs = await axios.get(`${this.$store.state.server}/charges`);
    if(rs.status === 200) {
      this.requests = rs.data;
    } else {
      alert(rs.data)
    }
  },
};
</script>

<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

@media (min-width: 800px) {
  section {
    grid-template-columns: 1fr 1fr;
  }
}
</style>
